.offset-item {
    opacity: 0;
    visibility: hidden;
    transform: translate3d(0, 30px, 0);
    -webkit-transform: translate3d(0, 30px, 0);
    transition: all 1s ease-in-out 0s;
    -webkit-transition: all 1s ease-in-out 0s;
}
.offset-item.animate {
    opacity: 1;
    visibility: visible;
    transform: none;
    -webkit-transform: none;
    transition: all 1s ease-in-out 0s;
    -webkit-transition: all 1s ease-in-out 0s;
}
