.site-header {
    padding: 6px 0;
    background-color: #ffffff;
    z-index: 1000;
    position: relative;
}
.site-header .container-fluid {
    padding-left: 50px;
    padding-right: 50px;
}
.site-header.float-header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-color: transparent;
}
.main-menu {
    display: inline-block;
    letter-spacing: normal;
}
.main-menu li {
    list-style: none;
    display: inline-block;
    position: relative;
    line-height: 40px;
    margin-right: 25px;
}
.main-menu li a {
    font-weight: 500;
    font-family: 'Jost';
}
.main-menu li i {
    font-size: 12px;
}
.main-menu .sub-menu {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    top: 100%;
    left: 0;
    font-size: 15px;
    min-width: 240px;
    background: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 50px;
    z-index: 9;
    -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
    -moz-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
}
.main-menu .sub-menu li {
    display: block;
    margin-right: 0;
    padding: 8px 15px;
    text-align: left;
    line-height: normal;
}
.main-menu .sub-menu li .sub-menu {
    top: 0;
    left: 100%;
}
.main-menu .sub-menu a {
    display: block;
    position: relative;
}
.main-menu .sub-menu a i {
    position: absolute;
    top: 3px;
    right: 0;
    transform: rotate(-90deg);
}
.main-menu li:hover > .sub-menu {
    opacity: 1;
    visibility: visible;
    margin-top: 0;
    -webkit-transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    -o-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
.btn {
    border-radius: 20px;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    background-color: #500C33;
    height: 40px;
    line-height: 40px;
    font-weight: 500;
    padding: 0 25px;
    color: #ffffff;
    outline: 0;
    outline-offset: 0;
}
.btn:hover {
    color: #ffffff;
    opacity: 0.7;
}
.btn:focus {
    box-shadow: none;
    color: #ffffff;
}
.popup-background {
    background-color: rgba(0, 0, 0, 0.8);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    display: none;
}
.popup {
    position: fixed;
    top: 0;
    left: -280px;
    bottom: 0;
    width: 280px;
    background-color: #ffffff;
    box-shadow: -2px 0 4px 0 rgba(0, 0, 0, 0.2);
    z-index: 1000;
    overflow: auto;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
}
.popup::-webkit-scrollbar {
    width: 0px;
    background-color: #F5F5F5;
}
.popup::-webkit-scrollbar-thumb {
    background-color: #500C33;
    border-radius: 10px;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
}
.popup::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}
.popup .popup__close {
    position: absolute;
    top: 32px;
    right: 25px;
    cursor: pointer;
    line-height: 0;
    z-index: 9;
}
.popup .popup__close i {
    font-size: 20px;
}
.popup .popup__minicart {
    padding: 0 25px;
}
.popup .popup__button {
    border-bottom: 0;
}
.popup .popup__user {
    padding: 30px 25px;
}
.popup .popup__user a {
    font-family: "Jost";
    font-size: 15px;
    font-weight: 500;
    margin-right: 25px;
}
.popup .popup__user a:last-child {
    margin-right: 0;
}
.popup .popup__cart {
    padding: 30px 25px;
}
.popup .popup__cart i {
    font-size: 26px;
    vertical-align: -2px;
}
.popup__box {
    padding: 25px;
    border-bottom: 1px solid #eeeeee;
    line-height: normal;
}
.popup__box li {
    list-style: none;
    display: block;
    margin-bottom: 25px;
}
.popup__box li:last-child {
    margin-bottom: 0;
}
.popup__box li a {
    font-family: "Jost";
    font-size: 15px;
    font-weight: 500;
    margin-right: 25px;
    margin-right: 0;
}
.popup__box li a i {
    vertical-align: 1px;
    margin-left: 3px;
    transition: all 0.3s;
    font-size: 12px;
}
.popup__box li a.open i {
    transition: all 0.3s;
    transform: rotate(180deg);
}
.popup__box li .sub-menu {
    padding-left: 22px;
    margin-top: 25px;
    display: none;
}
.popup--right {
    right: -280px;
    left: auto;
    text-align: left;
}
.popup--right .popup__button {
    padding: 0 25px;
}
.popup--right .popup__button .btn {
    width: 100%;
}
.popup--right .popup__box:last-child {
    border-bottom: 0;
}
.popup.popup--right.open {
    right: 0;
    left: auto;
}
.popup.open {
    opacity: 1;
    visibility: visible;
    left: 0;
}
.product-small {
