.model{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    background-color: rgba(0,0,0,0.7);
    justify-content: center;
    transition: opacity .4s ease,visibility .4s ease,transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
}
.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    z-index: 999;
}
